// Importing the modules
import React, {useEffect} from 'react'
import {Link} from 'react-router-dom'
import { useDispatch, useSelector} from 'react-redux'
import {Row, Col, Image, ListGroup, Form, Button, Card} from 'react-bootstrap'

// Importing the actions to be dispatch
import { addToCart, removeFromCart } from '../actions/cartActions'

// Importing the components 
import Message from '../components/Message'

const CartPage = ({match, location, history}) => {
    const productId = match.params.id

    const qty = location.search ? Number(location.search.split('=')[1]) : 1

    const dispatch = useDispatch()

    const cart = useSelector((state) => state.cart)
    const {cartItems} = cart

    useEffect(() => {
        if(productId) {
            dispatch(addToCart(productId, qty))
        }
    }, [dispatch, productId, qty])

    const removeFromCartHandler = (id) => {
        dispatch(removeFromCart(id))
    }

    const checkoutHandler = () => {
        history.push(`/login?redirect=shipping`)
    }

    return (
        <Row>
            <Col md={8}>
                <h1>Shopping Cart</h1>
                {cartItems.length === 0 ? <Message>Your cart is empty <Link to='/'>Go Back</Link></Message> : ( 
                    <ListGroup variant="flush">
                        {cartItems.map(item => (
                            <ListGroup.Item key={item.product}>
                                <Row>
                                    <Col md={2} className="my-2">
                                        <Image src={item.image} alt={item.name} fluid rounded />
                                    </Col>  
                                    <Col md={3} className="my-2">
                                        <Link className='cart-product-name' to={`/product/${item.product}`}>{item.name}</Link>
                                    </Col>   
                                    <Col md={2} className="my-2">${item.price}</Col>
                                    <Col md={2} className="my-2">
                                        <Form.Control as="select" custom value={item.qty} onChange={(e) => dispatch(addToCart(item.product, Number(e.target.value)))} >
                                            {[...Array(item.countInStock).keys()].map((x) => (
                                                <option key={x+1} value={x+1}>
                                                    {x+1}
                                                </option>
                                            ))}
                                        </Form.Control>
                                    </Col> 
                                    <Col md={2}>
                                        <Button type="button" variant="light" onClick={() => removeFromCartHandler(item.product)}>
                                            <i className='fas fa-trash'></i>
                                        </Button>
                                    </Col>
                                </Row>
                            </ListGroup.Item>
                        ))}
                    </ListGroup>
                )}
            </Col>
            <Col md={4}>
                <Card>
                    <ListGroup variant="flush">
                        <ListGroup.Item>
                            <h2>Subtotal ({cartItems.reduce((acc, item) => acc + item.qty, 0)}) items</h2>
                            <h4>Total: ${cartItems.reduce((acc, item) => acc + item.qty * item.price, 0)}</h4>                           
                        </ListGroup.Item>
                        <ListGroup.Item>
                            <Button 
                                type="button"
                                className="col-12"
                                disabled={cartItems.length === 0}
                                onClick={checkoutHandler}
                            >
                                Proceed To Checkout
                            </Button>
                        </ListGroup.Item>
                    </ListGroup>
                </Card>
            </Col>
        </Row>
    )
}

export default CartPage
